.container{
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  width: 100%;
}

.app{
  width: 350px;
  height: auto;
  background-color: rgb(34,34,34);
  padding:  0.5rem;
  border-radius: 5px;
  box-shadow: 2px 4px 8px 1px #06060675;
}

.image_container{
  display: flex;
  justify-content: center;
  padding: 10px 0;
}
.image{
  width: 200px;
  height: 200px;
  border-radius: 100px;

}
.cardContainer{
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  margin: 20px 0;
}

